function HideToolBar() {
  controlPanel.className = ''
}

function onButtonsClick() {
  let ele
  let mode = gps.getMode()

  ele = document.getElementById('mode-drag')
  if (mode == 'drag')
    ele.className='active'
  else
    ele.className=''

  ele = document.getElementById('mode-measuring')
  if (mode == 'measuring')
    ele.className='active'
  else
    ele.className=''

  ele = document.getElementById('mode-point')
  if (mode == 'point')
    ele.className='active'
  else
    ele.className=''
    
  ele = document.getElementById('mode-route')
  if (mode == 'route')
    ele.className='active'
  else
    ele.className=''  

  HideToolBar()
}

