onClick('load', function() {
  var el =  document.getElementById('list')
  el.className = 'list'
  el.innerHTML = ''

  for(var key in localStorage)
  {
    if (key.indexOf('Track_')==0) {
      let newItem = document.createElement('button');
      newItem.innerHTML = key
      newItem.className='btn-list'
      newItem.onclick = function(evt){
        gps.loadTrack(evt.target.innerHTML)
        el.className = ''
      };
      el.appendChild(newItem);

      let newDelItem = document.createElement('button');
      newDelItem.innerHTML = 'x'
      newDelItem.className='btn-list btn-del'
      newDelItem.id = key
      newDelItem.onclick = function(evt){
        //gps.deleteTrack(evt.target.innerHTML)
        if (confirm("Do you want to DELETE record \""+ evt.target.id +"\" ?")) {
          localStorage.removeItem(evt.target.id)
          el.className = ''
          notify.set('The record "' + evt.target.id + '" is deleted.')
        }
      };
      el.appendChild(newDelItem);      

    }
  }

  let newItem = document.createElement('button');
  newItem.innerHTML = 'Cancel'
  newItem.className='btn-list'
  newItem.onclick = function() {
    el.className = ''
  };
  el.appendChild(newItem);
  HideToolBar()
});


onClick('toolbar', function() {
  if (controlPanel.className == '')
    controlPanel.className = 'show'
  else
    controlPanel.className = ''
});


