'use strict';

const Notify = function(ElementId) {
    this.ElementId = ElementId

    this.set = function(text, type='info', time=2) {
        let ele = document.getElementById(this.ElementId)
        ele.innerHTML = text
        ele.className = 'notify-' + type
        setTimeout(()=>{
            ele.className = 'notify-hidden'
            ele.innerHTML = ''
        }, time * 1000);
    }
}

export default Notify
